import React from 'react'
import styled from 'styled-components'

const Headerstyle=styled.h2`
text-decoration-color:burlywood;
color:#cc3300;
font-size:16px;
font-weight:7rem;
`

const Formstyle=styled.div`
 background-color: tomato;
  width: 200px;
  border: 3px solid #cc3300;
  padding: 30px;
  margin: 10px;

 animation: mymove 5s infinite;

 @keyframes mymove {
  from {background-color: whitesmoke;}
  to {background-color: greenyellow;}
 }
`

const Textareastyle=styled.div`

color:#cc3300;
font-variant:normal;
font-size:18px;
font-weight:bolder;
background-color: transparent;
background-position: 10px 10px; 
background-repeat: no-repeat;
border:none;

 `

const Button=styled.button`
color:#cc3300;
margin-top:4px;
width:40px;
font-weight:10;
font-variant:normal;
font-size:14px;
text-shadow:olive;
background-color:snow;
border-color:#cc3300;
font-weight:bolder;
`
 
 

const App=({persons,addperson,handlename,handlenumber,newName,newNumber})=>{
   
       return(
          <>
           <div>
     <Headerstyle>
     <h2>Add a new</h2>
     </Headerstyle>
     <Formstyle>
     <form onSubmit={addperson}>
      
       <div>
       <Textareastyle>
       name:
      
          <input value={persons.find(val=>val.name!==newName)?newName:alert(`${newName} wxist`)} 
          onChange={handlename}/>
       </Textareastyle>
        </div>
     
      
       <div>
         <Textareastyle>
         number: <input value={newNumber}
          onChange={handlenumber} />
         </Textareastyle>
       
        </div>
      

        <div>

          <Button type="submit">add</Button>
        </div>
      </form>
     </Formstyle>
     
      </div>
          </>
      )
}

export default App