import React, { useState,useEffect } from 'react'
import styled from 'styled-components'
import Addnew from './components/addperson/Addperosn'
import Filter from './components/Filter/Filter'
import Numbers from './components/perosns/people'
import phnbkservice from '../src/services/phnbk'
import Notify from './components/notification/notify'


const Phonebookstyle=styled.div`
width:100%;
display:inline-flex;
align-items:center;
justify-content:center;
background-clip:padding-box;
background-color:#1c020d;
height:570px;

`

const Headstyle=styled.h2`
text-decoration-color:burlywood;
color:#cc3300;
font-size:16px;
font-weight:7rem;
margin-left:1px;
`

const Div1=styled.div`
padding-right:20px;
`

const Errorstyle=styled.div`
color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  margin-bottom:10px;
  padding:10px;
`

const App = () => {
const [ persons, setPersons ] = useState([{id:1, name: 'Arto Hellas',number: 653345345 }]) 
const [ newName, setNewName ] = useState('')
const [ newNumber, setNewNumber ] = useState('')
const [errorMessage, setErrorMessage] = useState('some error happened...')


useEffect(() => {
  phnbkservice
    .getAll()
    .then(initialNotes => {
      setPersons(initialNotes)
    })
}, [])

console.log('render', persons.length, 'people')



const okdelete = id => {
  const person = persons.find(n => n.id === id)

  phnbkservice
  .dodelete(id)
    .then(returnedNote => {
    setPersons(persons.filter(note => note.id !==person.id ? note : returnedNote))
    })
    .catch(error => {
      setErrorMessage(
        `people '${person.name}' was already removed from server`
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
})

}



  const handlenewname=(event)=>{
      setNewName(event.target.value)
       console.log(event.target.value)
     }
    
     const handlenewnumber=(event)=>{
     setNewNumber(event.target.value)
      console.log(event.target.value)
    }
    
    const Addperson=(event)=>{
    event.preventDefault()
    const person={
      name:newName,
      number:newNumber,
      id:Math.floor(Math.random()*21),
      date:new Date()
    }
    phnbkservice
    .create(person)
    .then(returnedNote => {
      setPersons(persons.concat(returnedNote))
      setNewName('')
      setNewNumber('')
      setErrorMessage(
        `people '${person.name}' added to server`
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    })
    
     
  }

  return (
    <>
 <Errorstyle>
  <Notify message={errorMessage}/>
  </Errorstyle>
<Phonebookstyle>
<div>
  <Div1>
  <Filter persons={persons}/>
  </Div1>
   
    </div>
    <div>
      <Div1>
      <Addnew persons={persons} handlename={handlenewname}
       handlenumber={handlenewnumber}addperson={Addperson} newName={newName}
      newNumber={newNumber}
      />
      </Div1>
    
    </div>


    <div>
      <Div1>
      <Headstyle>
      <h2>Numbers</h2>
      </Headstyle>
     
      <div>
      <ul>
 
 {persons.map((note) => 
          <Numbers
            name={note.name}  number={note.number}
            okdelete={()=>okdelete(note.id)}
             />
 
   )}
      </ul>
      </div>
      </Div1>
    
      </div>
   </Phonebookstyle>

   
   </>
  )
}

export default App