import React,{useState} from 'react'
import styled from 'styled-components'


const Headstyle=styled.h2`
text-decoration-color:burlywood;
color:#cc3300;
font-size:16px;
font-weight:7rem;
`
const Fieldstyle=styled.div`
background-color:tomato;
 width: 200px;
  border: 3px solid #cc3300;
  padding: 30px;
  margin: 10px;
  animation: mymove 5s infinite;
   @keyframes mymove {
  from {background-color:wheat;}
  to {background-color: seashell;}
 }
`
const Textareastyle=styled.div`

color:#cc3300;
font-variant:normal;
font-size:18px;
font-weight:bolder;
background-color: transparent;
background-position: 10px 10px; 
background-repeat: no-repeat;
border:none;


 `

 const Liststyle=styled.div`
 width:100%;
 color:#54404b;
 font-size:16px;
 font-family:serif;
 font-style:italic;
 list-style: none;
 `


const Filter=({persons})=>{
    const [filtered,setfiltered]=useState('search by name')

     const filteredname=persons.filter(
        name=>name.name.toLowerCase().includes(filtered.toLowerCase()))
      

        const handlefilteredChange = (event) => {
            console.log(event.target.value)
            setfiltered(event.target.value)
          }


return (
   <>
     <div>
       <Headstyle>
       <h2>
         Phonebook
         </h2>
       </Headstyle>
        </div>
<Fieldstyle>
<div>
  <Textareastyle>
  search:<input value={filtered} 
        onChange={handlefilteredChange}/>
  </Textareastyle>
        </div>

         <div>
          
           <ul>
           <Liststyle>
        {filteredname.map((name) => 
          <li>{name.name} {name.number}</li>  )}
          </Liststyle>
      </ul>
         
      
         </div>
</Fieldstyle>
     
     
         </>
)
        }
         export default Filter
