import React from 'react'
import styled from 'styled-components'

const Personstyle=styled.div`
 background-color: tomato;
  width: auto;
  border: 1.4px solid #cc3300;
  padding: 2px;
  margin: 2px;
 height:auto;
animation: mymove 5s infinite;
  @keyframes mymove {
  from {background-color: black;}
  to {background-color: goldenrod;}
}

`
const Popstyle=styled.p`

text-shadow:white;
font-size:18px;
color:#922f17;
font-weight:bold;
`

const Button=styled.button`
color:#cc3300;
margin-top:4px;
width:80px;
font-weight:10;
font-variant:normal;
font-size:14px;
text-shadow:olive;
background-color:snow;
border-color:#cc3300;
font-weight:bolder;
`



const Person=({name,number,okdelete})=>{
return(
<>
<div>
  <Personstyle>
  <Popstyle>
  {name} {number} <Button onClick={okdelete}>delete</Button>
    </Popstyle>
  </Personstyle>

</div>
 
  </>
    )
}
export default Person